
html {
  font-family: 'Arial,Verdana,Sans-serif';
  height: 100%;
  width: 100%;
  -webkit-text-size-adjust: none;
  word-wrap: break-word;
  -webkit-font-smoothing: antialiased;
}
body {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  font-size: 0.2rem;
  color: #333;
}
body::-webkit-scrollbar {
  width: 0;
}
html,
