h1,
h2,
h3,
h4,
h5,
h6,
p,
dl,
dd,
ol,
ul,
th,
td,
form,
fieldset,
input,
button,
textarea {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-style: normal;
}
a {
  text-decoration: none;
  color: inherit;
}
div {
  box-sizing: border-box;
}
li {
  list-style-type: none;
}
